.heyIcon:hover {
  font-size: xx-large;
  color: rgb(10, 10, 10);
  background: rgba(250, 3, 3, 0.4);
  border-radius: 30%;
  cursor: pointer;
}

nav {
  background: rgba(0, 0, 0, 0.3);
  width: max-content;
  display: block;
  padding: 0.7rem 1.7rem;
  z-index: 2;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 2rem;
  display: flex;
  gap: 2rem;
  border-radius: 3rem;
  backdrop-filter: blur(15px);
  color: white;
}

nav a {
  background: transparent;
  color: white;
  padding: 0.4rem;
  border-radius: 50%;
  display: flex;
  font-size: 1.1rem;
}

nav a.active {
  background: white;
  color: black;
}
